<template>
    <div class="articleBox" ref="articleBox">
        <!--  -->
        <div class="header">
            <button @click="goBack" class="back-button"></button>
            <span class="separator">/</span>
            <span class="title">{{ article?.title }}</span>
        </div>

        <div class="articleText" v-if="article">
            <div class="articleImgTle">
                <div class="img-container">
                    <img :src="getRandomImage()" alt="Random Image" class="article-image" />
                    <div class="overlay">
                        <span class="title">{{ article?.title }}</span>
                        <p>{{ article.BriefIntroduction }}</p>
                        <p class="article-author"><strong> : </strong> {{ article.author }}</p>
                    </div>
                </div>
            </div>

            <div class="articleContent">
                <div class="articletextS" v-html="article.content"></div>
            </div>
        </div>


        <div v-else>
            <p>Loading...</p>
        </div>

        <!--  -->
        <section class="comment-section">
            <h2></h2>
            <div class="comment-input">
                <textarea v-model="newComment" placeholder="..."></textarea>
                <button @click="submitComment"></button>
            </div>
            <div class="comments-list">
                <h3></h3>
                <div v-for="comment in comments" :key="comment._id" class="comment">
                    <p>{{ comment.content }}</p>

                    <small v-if="comment.author && comment.createdAt">
                        by {{ comment.author.username }}  {{ new Date(comment.createdAt).toLocaleString() }}
                    </small>
                    <small v-else>
                        Unknown author  Unknown date
                    </small>

                    <!--  -->
                    <div class="replies" v-if="comment.replies && comment.replies.length">
                        <div v-for="reply in comment.replies" :key="reply._id" class="reply">
                            <p>{{ reply.content }}</p>
                            <small v-if="reply.author && reply.createdAt">
                                by {{ reply.author.username }}  {{ new Date(reply.createdAt).toLocaleString() }}
                            </small>
                            <small v-else>
                                Unknown author  Unknown date
                            </small>
                        </div>
                    </div>

                    <!--  -->
                    <button @click="replyToComment(comment._id)"></button>

                    <!--  -->
                    <div v-if="replyingTo === comment._id" class="reply-form">
                        <textarea v-model="replyContent" placeholder="..."></textarea>
                        <button @click="submitReply(comment._id)"></button>
                        <button @click="cancelReply"></button>
                    </div>
                </div>
            </div>
        </section>


    </div>
</template>

<script>
// Article.vue
import axios from 'axios';
import colors from 'vuetify/lib/util/colors';

export default {
    data() {
        return {
            newComment: '', // 
            comments: [],   // 
            article: null,
            replyingTo: null, //  ID
            replyContent: '', // 
            progressBarWidth: 0, // 
            scrollPosition: 0,  // 
            articleBoxHeight: 0, // 
            acid: "",
            images: [
                { src: require("../assets/1.jpg") },
                { src: require("../assets/2.jpg") },
            ],
            themes: {
                light: {
                    '--primary-color': '#3498db',
                    '--ZiBaiBgc': "#f8f9ff",
                    '--ActiveBgc': '#f7f7f8',
                    '--background-color': '#ffffff',
                    '--Border': ' #f0f1fb',
                    '--text-color': '#000000',//
                    '--active-background-color': '#1988fa',//
                    '--article-card-background-color': ' #f5f5f5',//
                    '--Business-card-gradient': 'linear-gradient(to right, #1988fa 0%, #33c4f9 50%, #00f2fe 100%)'
                },
                dark: {
                    '--primary-color': '#e74c3c',
                    '--ActiveBgc': '#1a1a1a',
                    '--ZiBaiBgc': "#1f1f1f",
                    '--background-color': '#000000',
                    '--Border': ' #2c2c2c',
                    '--text-color': '#ecf0f1',//
                    '--active-background-color': '#015aea',//
                    '--article-card-background-color': ' #212121',//
                    '--Business-card-gradient': 'linear-gradient(to right, #012a63, #015aea, #4d9ef7)'
                }
            },
        };
    },

    created() {
        const articleId = this.$route.params.id; //  ID
        this.acid = articleId;
        this.fetchComments(articleId); // 
        this.fetchArticleData();
    },

    mounted() {
        const savedTheme = localStorage.getItem('theme') || 'light';


        this.updateTheme(savedTheme);

        console.log('', savedTheme);  // 



        this.$nextTick(() => {
            this.handleScroll(); // 
            window.addEventListener("scroll", this.handleScroll);
        });


        // 
        this.getLastViewedArticle().then(savedScrollPosition => {
            //  articleBox 
            setTimeout(() => {
                if (savedScrollPosition !== null) {
                    window.scrollTo({
                        top: savedScrollPosition,
                        behavior: 'smooth'
                    });

                }
            }, 100);  //  100ms 
        });

        // 
        window.addEventListener('scroll', this.handleScroll);
    },

    computed: {

    },
    beforeDestroy() {
        const articleBox = this.$refs.articleBox;

        // 
        if (articleBox) {
            articleBox.removeEventListener('scroll', this.handleScroll);
        }

        // 
        this.saveScrollPosition();
        console.log('');

        // 
        window.removeEventListener('resize', this.handleResize);
    },

    methods: {
        updateTheme(themeName) {
            console.log(themeName);

            // document.body.classList.toggle('dark-theme', themeName === 'dark');
            this.currentTheme = themeName;//currentThemedatahtml
            //  CSS   
            const theme = this.themes[themeName];
            for (const key in theme) {
                document.documentElement.style.setProperty(key, theme[key]);
            }
            //  localStorage 
            localStorage.setItem('theme', themeName);
        },
        handleScroll() {
            const articleBox = this.$refs.articleBox;
            if (articleBox) {
                this.scrollPosition = window.scrollY;
                this.articleBoxHeight = articleBox.clientHeight;
                // console.log(":", this.articleBoxHeight);
                // console.log(":", this.scrollPosition);
            }
        },
        async getLastViewedArticle() {
            if (!localStorage.getItem('token')) {
                return null
            } else {
                console.log("token");
            }
            console.log(localStorage.getItem('token'));

            try {
                // 
                const response = await axios.get(`/api/user/last-viewed/${this.acid}`, {
                    headers: { 'Authorization': `Bearer ${localStorage.getItem('token')}` }
                });
                // console.log("", response.data);
                return response.data.scrollPosition;

            } catch (error) {
                console.error('Error retrieving last viewed article:', error);
                return null;
            }
        },


        async saveScrollPosition() {//
            if (!localStorage.getItem('token')) {
                return
            }//token
            console.log(this.articleBoxHeight);
            console.log(window.innerHeight);

            const maxScrollPosition = this.articleBoxHeight - window.innerHeight;//
            console.log('', maxScrollPosition);

            try {
                // 
                await axios.post('/api/user/save-progress', {
                    articleId: this.acid,
                    scrollPosition: this.scrollPosition,
                    maxScrollPosition: maxScrollPosition // 
                }, {
                    headers: { 'Authorization': `Bearer ${localStorage.getItem('token')}` }
                });
                // console.log(":", scrollPosition);
            } catch (error) {
                console.error('Error saving scroll position:', error);
            }

        },

        handleResize() {
            //  articleBox 
            const articleBox = this.$refs.articleBox;
            articleBox.style.height = `${window.innerHeight}px`;
        },

        goBack() {
            this.$router.go(-1); //  Vue Router 
        },

        getRandomImage() {
            const randomIndex = Math.floor(Math.random() * this.images.length);
            return this.images[randomIndex].src;
        },

        async fetchArticleData() {
            try {
                const articleId = this.$route.params.id;
                // const token = localStorage.getItem('token'); //  localStorage  Token
                const response = await axios.get(`/api/articles/${articleId}`);

                this.article = response.data;

            } catch (error) {
                console.error('Error fetching article data:', error);
            }
        },

        replyToComment(commentId) {
            this.replyingTo = commentId;
            this.replyContent = ''; // 
        },

        cancelReply() {
            this.replyingTo = null;
            this.replyContent = '';
        },
        //
        async submitComment() {
            if (!this.newComment.trim()) {
                alert('');
                return;
            }

            try {
                const response = await axios.post('/api/comments', {
                    content: this.newComment,
                    articleId: this.acid,
                    author: this.user
                }, {
                    headers: { 'Authorization': `Bearer ${localStorage.getItem('token')}` }
                });

                this.newComment = '';
                this.comments.push(response.data.comment);
            } catch (error) {
                console.error('Error submitting comment:', error);
            }
        },
        //
        async submitReply(parentCommentId) {
            if (!this.replyContent.trim()) {
                alert('');
                return;
            }

            try {
                await axios.post('/api/comments/reply', {
                    content: this.replyContent,
                    articleId: this.article._id,
                    parentCommentId: parentCommentId
                }, {
                    headers: { 'Authorization': `Bearer ${localStorage.getItem('token')}` }
                });

                this.fetchComments(this.article._id);
                this.replyingTo = null;
                this.replyContent = '';
            } catch (error) {
                console.error('Error submitting reply:', error);
            }
        },

        async fetchComments(articleId) {
            // 
            try {
                // GET
                console.log(articleId);

                const response = await axios.get(`/api/comments/${articleId}/comments`, {

                    headers: { 'Authorization': `Bearer ${localStorage.getItem('token')}` }

                });

                console.log("");

                // this.comments
                this.comments = response.data.comments;
            } catch (error) {
                // 
                console.error('Error fetching comments:', error);
            }
        },


    }
};
</script>

