<template>
    <div class="settings">
        <h2></h2>
        <input type="checkbox" class="theme-checkbox" @click="toggleTheme" :checked="currentTheme === 'dark'">
    </div>
</template>

<script>
export default {
    data() {
        return {
            currentTheme: 'light'  // 
        };
    },
    methods: {
        toggleTheme() {
            // 
            this.currentTheme = this.currentTheme === 'light' ? 'dark' : 'light';

            // 
            this.$emit('themeChanged', this.currentTheme);

            //  localStorage
            localStorage.setItem('theme', this.currentTheme);
        },
        async uploadAvatar(event) {//
            // 
            const file = event.target.files[0]; // 
            const formData = new FormData(); //  FormData 
            formData.append("avatar", file); //  FormData  "avatar"

            try {
                //  POST 
                const response = await axios.post(
                    "/api/user/update-avatar", //  API 
                    formData, //  FormData 
                    {
                        headers: {
                            Authorization: `Bearer ${localStorage.getItem("token")}`, //  token
                        },
                    }
                );

                //  Vuex 
                this.getUser.avatar = response.data.avatar; //  URL 


            } catch (error) {
                // 
                console.error("Error updating avatar:", error); // 
            }
        },
    },
    mounted() {
        // 
        const savedTheme = localStorage.getItem('theme');
        if (savedTheme) {
            this.currentTheme = savedTheme;
            this.$emit('themeChanged', this.currentTheme);  // 
        }
    }
};
</script>

