<template>
  <div id="app">
    <!--  Loader  loading  -->
    <transition name="fade">
            <Loader v-if="isLoading" :visible="isLoading" />
        </transition>
    
    <!--  -->
    <router-view class="view"></router-view>
  </div>
</template>

<script>
import Loader from "@/components/Loader.vue";
import { mapState, mapActions } from 'vuex';
export default {
  data() {
    return {
      isLoading: false,  // 
    };
  },
  components: {
    Loader,
  },
  watch: {
    // 
    $route(to) {
      this.onRouteChange(to);
    }
  },
  computed: {
    ...mapState(['hasVisitedHome']), //  Vuex  hasVisitedHome 
  },
  methods: {
    ...mapActions(['markVisitedHome']), //  Vuex  action
    onRouteChange(to) {
      console.log('');
      
      //  home 
      if (to.name === 'Home' && !this.hasVisitedHome) {
        console.log('');
        this.isLoading = true;  // 
        // 
        setTimeout(() => {
          this.isLoading = false;  // 
          this.markVisitedHome();  //  Vuex 
        }, 3000); // 
      } else {
        this.isLoading = false;  //  home
      }
    }
  },
  mounted() {
     // 
     this.onRouteChange(this.$route);
  }
};
</script>

<style>
.fade-enter-active,
.fade-leave-active {
  transition: opacity 1s;
}

#app {
  background-color: var(--background-color);
  transition: background-color 0.5s ease, color 0.5s ease;
  height: 100vh;
  min-width: 320px;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}

a {
  text-decoration: none !important;
  line-height: 0 !important;
}
</style>
