import Vue from 'vue'
import Vuex from 'vuex';
import ElementUI from 'element-ui'
// ElementUI
import 'element-ui/lib/theme-chalk/index.css'
// ElementUI

import Index from '../views/index.vue'
// index.vue
import Login from '../views/Login.vue'
import Home from '../components/IndeHome.vue'
import Categories from "../components/IndexCategories.vue"
import Content from '../components/IndexContent.vue'
import Settings from '../components/IndexSettings.vue'
import VueRouter from 'vue-router'
import Article from '../views/Article.vue'
import AppUser from '../components/AppUser.vue';
import { isTokenExpired } from '@/utils/auth';
import { Notification } from 'element-ui';
// ElementUI
Vue.use(ElementUI)
// VueRouter
Vue.use(VueRouter)
// 
// 
const routes = [
    // Index
    {
        path: '/',
        redirect: '/Index',
    },
    // Index
    {
        path: '/Index',
        component: Index,
        name: 'Index',
        meta: { requiresAuth: true }, // 
        children: [
            {
                path: '', // 
                redirect: 'home' //  home

            },
            {
                path: 'home', // 
                component: Home,
                name: 'Home',
                meta: { requiresAuth: true }, // 
            },
            {
                path: 'categories',
                component: Categories,
                name: "Categories",
                meta: { requiresAuth: true }, // 
            },
            {
                path: 'content', // 
                component: Content,
                name: 'Content',
                meta: { requiresAuth: true }, // 
                beforeEnter: (to, from, next) => {//token
                    const token = localStorage.getItem('token');
                    if (!token) {
                        return
                    } else {
                        next(); //  token
                    }
                }
            },
            {
                path: 'appuser', // 
                component: AppUser,
                name: 'AppUser',
                meta: { requiresAuth: true }, // 
            },
            {
                path: 'settings',
                component: Settings,
                name: "Settings",
                meta: { requiresAuth: true }, // 

            },
        ]
    },

    // Entry

    {
        path: '/Login',
        component: Login,
        name: 'Login'
    },

    {
        path: '/Article/:id', // :id 
        name: 'Article',
        component: Article,
        meta: { requiresAuth: true }, // 
    }
]

const router = new VueRouter({
    routes,
})
// 
router.beforeEach((to, from, next) => {
    const token = localStorage.getItem("token");
    console.log('', token);

    //  token 
    if (to.matched.some(record => record.meta.requiresAuth)) {
        if (!token || isTokenExpired()) {
            console.log("token ");
            Notification.error({
                title: "",
                message: "",
                duration: 3000,
            });
            localStorage.removeItem("token"); //  token
            next('/Login'); //  token 
        } else {
            next(); // token 
        }
    } else {
        next(); // 
    }
});
export default router
