<template>
  <div class="upload-article-container">
    <el-form ref="uploadForm" :model="formData" :rules="rules" label-width="100px">
      <!--  -->
      <el-form-item label="" prop="title">
        <el-input v-model="formData.title" placeholder=""></el-input>
      </el-form-item>

      <!--  -->
      <el-form-item label="" prop="author">
        <el-input v-model="formData.author" placeholder=""></el-input>
      </el-form-item>

      <!--  -->
      <el-form-item label="" prop="BriefIntroduction">
        <el-input v-model="formData.BriefIntroduction" type="textarea" placeholder="" rows="4"></el-input>
      </el-form-item>

      <!--  -->
      <el-form-item label="" prop="content">
        <quill-editor v-model="formData.content" :options="editorOptions" class="quill-editor"></quill-editor>
      </el-form-item>

      <!--  -->
      <el-form-item label="" prop="categories">
        <el-select v-model="formData.categories" placeholder="" style="width: 300px">
          <el-option v-for="category in categories" :key="category._id" :label="category.name"
            :value="category._id"></el-option>
        </el-select>
      </el-form-item>

      <!--  -->
      <el-form-item label="" prop="coverImage">
        <el-upload class="upload-demo" action="" :file-list="fileList" :on-change="handleFileUpload"
          :auto-upload="false">
          <el-button></el-button>
        </el-upload>
      </el-form-item>

      <!--  -->
      <el-form-item>
        <el-button type="primary" @click="saveContent"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import axios from "axios";
import { debounce } from "lodash";
import { Notification } from 'element-ui';
export default {
  data() {
    return {
      formData: {
        title: "",
        author: "",
        BriefIntroduction: "",
        content: "",
        categories: null,
        coverImage: null,
      },
      rules: {
        title: [{ required: true, message: "", trigger: "blur" }],
        author: [{ required: true, message: "", trigger: "blur" }],
        BriefIntroduction: [{ required: true, message: "", trigger: "blur" }],
        categories: [{ required: true, message: "", trigger: "change" }],
        content: [{ required: true, message: "", trigger: "blur" }],
      },
      categories: [], // 
      fileList: [],
      editorOptions: {
        theme: "snow",
        modules: {
          toolbar: [
            ["bold", "italic", "underline", "strike"],
            [{ list: "ordered" }, { list: "bullet" }],
            [{ align: [] }],
            ["clean"],
            ["image"], // 
          ],
        },
      },
    };
  },
  created() {
    this.fetchCategories(); // 
  },
  methods: {
    async fetchCategories() {
      try {
        const response = await axios.get("/api/categories");
        this.categories = response.data;
      } catch (error) {
        console.error("Error fetching categories:", error);
      }
    },
    handleFileUpload(file, fileList) {
      this.formData.coverImage = file.raw;
      this.fileList = fileList;
    },
    saveContent() {
      this.$refs.uploadForm.validate(async (valid) => {
        if (!valid) {
          console.log("");
          return;
        }
        const formData = new FormData();
        Object.keys(this.formData).forEach((key) => {
          formData.append(key, this.formData[key]);
        });

        try {
          const token = localStorage.getItem("token");
          const response = await axios.post("/api/articles", formData, {
            headers: {
              Authorization: `Bearer ${token}`,
              "Content-Type": "multipart/form-data",
            },
          });
          Notification.success({
            title: '',
            message: '',
          });
          console.log("", response.data);
        } catch (error) {
          Notification.error({
            title: '',
            message: '',
          });
          console.error("", error);
        }
      });
    },
  },
};
</script>

