<template>
  <div class="profile-container">
    <!--  -->
   
    <div class="profile-info">
     
      <!--  -->
      <div class="profile-avatar" @click="openDialog">
        <img :src="getImageUrl(user.avatar,'UserImg')" alt="" />
      </div>
       <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible">
      <el-upload
        action="/api/update-avatar"
        :headers="headers" 
        name="avatar" 
        list-type="picture-card"
        :on-success="handleSuccess"
        :on-error="handleError"
      >
        <i class="el-icon-plus"></i>
      </el-upload>
      <p></p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"></el-button>
      </span>
    </el-dialog>
      <!--  -->
      <div class="username">
        <h3> <span>{{ user.username }}</span></h3>
      </div>
      <!--  -->
      <div class="user-account">
        <p><span>{{ user.email }}</span></p>
      </div>
      <button @click="LU_logout"></button>

    </div>

    <!--  -->
    <div class="record-section">
      <h4></h4>
      <ul>
        <li v-for="(item, index) in browsingHistory" :key="index">{{ item }}</li>
      </ul>
    </div>

    <!--  -->
    <div class="favorites-section">
      <h4></h4>
      <ul>
        <li v-for="(item, index) in favorites" :key="index">{{ item }}</li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
  data() {
    return {
      headers: {
        Authorization: `Bearer ${localStorage.getItem('token')}`, //  localStorage  token
      },
      user:"",
      // 
      browsingHistory: ["1", "2", "3"],
      dialogVisible: false, // 
      // 
      favorites: ["A", "B", "C"],
      isMobile: false// 
    };
  },
  computed: {
    ...mapGetters(['getUser', 'isLoggedIn']),
   
  },
  created() {
    this.user = this.getUser, // 
    this.checkDeviceType();
    window.addEventListener('resize', this.checkDeviceType);  // 
  },
  destroyed() {
    window.removeEventListener('resize', this.checkDeviceType);
  },
  methods: {
    LU_logout() {
            this.$emit('call-parent')
        },
    getImageUrl(imageName, type = 'uploads') {
            if (!imageName) {
                //  imageName  null 
                return null;
            }

            // 
            const isDevelopment = process.env.NODE_ENV === 'development';

            // 
            let imageUrl;
            if (isDevelopment) {
                //  localhost:3000
                imageUrl = `http://localhost:3000/${type}/${imageName}`;
            } else {
                // 
                imageUrl = `${window.location.origin}/${type}/${imageName}`;
            }

            console.log('', imageUrl);
            return imageUrl;
        },
    // 
    openDialog() {
      this.dialogVisible = true; // 
    },
    handleSuccess(response, file) {
      this.$message.success('');
      this.dialogVisible = false; // 
    },
    handleError(err) {
      this.$message.error('');
    },
    // PC
    checkDeviceType() {
      const width = window.innerWidth;
      this.isMobile = width <= 768; // 

      // PC
      if (!this.isMobile) {
        this.$router.push('/Index/Home');
      }
    },
  },
};
</script>

