<template>
    <div class="categories-section">
        <div class="categories-container">
            <div class="category-card" v-for="(category, index) in categorieslatestArticles" :key="index">
                <div class="category-image-wrapper">
                    <router-link :to="{
                        name: 'Article',
                        params: { id: category._id || '' },
                    }">
                        <img :src="getImageUrl(category.coverImage,'uploads')" alt="Category cover" class="category-cover" />
                        <div class="category-info">
                            <h3 class="category-title">{{ category.title }}</h3>
                            <p class="category-author">
                                <img :src="getImageUrl(authorAvatars[category.user],'UserImg')" alt="Author Avatar" class="author-avatar" />
                                <span>{{ category.author }}</span>
                            </p>
                        </div>
                    </router-link>
                </div>
            </div>
        </div>

        <div class="all-articles-container">
            <!-- <h2></h2> -->
            <div class="article-card" v-for="(article, index) in paginatedArticles" :key="index">
                <div class="article-cover">
                    <img :src="getImageUrl(article.coverImage,'uploads')" alt="Article cover" class="article-cover-image" />

                </div>
               
                <div class="article-details">
                    <router-link :to="{
                        name: 'Article',
                        params: { id: article._id || '' },
                    }">
                     <h3 class="article-title">{{ article.title }}</h3>
                     <p class="article-date">{{ article.BriefIntroduction }}</p>
                     <p class="article-description">{{ article.description }}</p>
                        <p class="article-author">
                            <img :src="getImageUrl(authorAvatars[article.user],'UserImg')" alt="Author avatar" class="author-avatar" />
                            <span>{{ article.author }}</span>
                        </p>
                       
                        
                        
                    </router-link>
                </div>
                <div >
                    <span v-if="userInformation" class="favorite-button" @click="toggleFavorite(article._id)">
                    <img :src="require(`@/assets/ShouCang-Huang.png`)" v-if="article.isFavorite" />
                    <img :src="require(`@/assets/ShouCang-Bai.png`)" v-else />
                </span>
                <span v-else></span>
                </div>
               
            </div>

            <!-- Element UI Pagination -->
            <el-pagination background layout="prev, pager, next" :total="categoriesarticles.length"
                :page-size="pageSize" :current-page="currentPage" @current-change="handlePageChange"></el-pagination>
        </div>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    data() {
        return {
            categoriesarticles: [], // 
            categorieslatestArticles: [], // 
            authorAvatars: {}, // key ID
            userInformation: '', // ID
            currentPage: 1, // 
            pageSize: 5, // 
            favorites: [], // 
        }
    },

    components: {},
    computed: {
        // 
        paginatedArticles() {
            const start = (this.currentPage - 1) * this.pageSize
            const end = start + this.pageSize
            return this.categoriesarticles.slice(start, end)
        },
    },
    watch: {},
    methods: {
        getImageUrl(imageName, type = 'uploads') {
            if (!imageName) {
                //  imageName  null 
                return null;
            }

            // 
            const isDevelopment = process.env.NODE_ENV === 'development';

            // 
            let imageUrl;
            if (isDevelopment) {
                //  localhost:3000
                imageUrl = `http://localhost:3000/${type}/${imageName}`;
            } else {
                // 
                imageUrl = `${window.location.origin}/${type}/${imageName}`;
            }

            console.log('', imageUrl);
            return imageUrl;
        },
        async categoriesfetchArticles() {
            try {
                // GET
                const response = await axios.get('/api/articles')
                this.categoriesarticles = response.data // 
                // console.log('', response.data)

                // 
                this.categorieslatestArticles = this.categoriesarticles.slice(-1) // 
                // 
                for (let article of this.categoriesarticles) {
                    if (article.user) {
                        this.fetchAuthorAvatar(article.user) // 
                    }
                }
            } catch (error) {
                console.error('Error fetching articles:', error)
            }
        },
        // 
        async fetchAuthorAvatar(userId) {
            try {
                const userResponse = await axios.get(`/api/public-user-info/${userId}`)
                this.$set(this.authorAvatars, userId, userResponse.data.avatar) // 

                // console.log('authorAvatars', this.authorAvatars)
            } catch (error) {
                console.error('Error fetching user avatar:', error)
            }
        },
        //
        // HomegetImageUrl(imageName) {
        //     return `http://localhost:3000/uploads/${imageName}`
        // },
        // 
        handlePageChange(page) {
            this.currentPage = page
        },
        async getuser() {
            if (!localStorage.getItem('token')) {//
                return;
            }
            const response = await axios.get('/api/user', {
                headers: {
                    Authorization: `Bearer ${localStorage.getItem('token')}`,
                },
            })

            this.userInformation = response.data._id //  response.data  _id
            // console.log('id', response.data);

            // console.log('', this.userInformation);
        },

        async toggleFavorite(articleId) {
            const userId = this.userInformation //  ID
            // console.log('id', userId);

            try {
                // 
                const response = await axios.get(`/api/favorites/${userId}/${articleId}`)

                console.log('', response);

                const isFavorite = response.data // 
                console.log('', isFavorite);

                if (isFavorite) {
                    // 
                    await axios.delete(`/api/favorites`, {
                        data: { userId, articleId },
                    })
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'warning'
                    });
                    const article = this.categoriesarticles.find((a) => a._id === articleId)
                    if (article) {
                        article.isFavorite = false // 
                    }
                } else {
                    // 
                    await axios.post(`/api/favorites`, {
                        userId,
                        articleId,
                    })

                    const article = this.categoriesarticles.find((a) => a._id === articleId)
                    this.$notify({
                        title: '',
                        message: '',
                        type: 'success'
                    });
                    console.log('')
                    // console.log('', article);

                    if (article) {
                        article.isFavorite = true // 
                    }
                }
            } catch (error) {
                console.error(':', error)
            }
        },
        //idididid
        async loadFavorites() {
            if (!localStorage.getItem('token')) {
                return;
            }
            const response = await axios.get('/api/user', {
                //
                headers: {
                    Authorization: `Bearer ${localStorage.getItem('token')}`,
                },
            })

            const LoaduserId = response.data._id //  ID

            try {
                const response = await axios.get(`/api/favorites/${LoaduserId}`)

                this.favorites = response.data; // 
                console.log('', this.favorites);

                // 
                if (!Array.isArray(this.favorites)) {
                    console.error("", this.favorites);
                    return;
                }



                this.categoriesarticles.forEach((article) => {
                    //  this.favorites  article._id 
                    article.isFavorite = this.favorites.some((fav) => {
                        console.log(":", fav.articleId._id);  //  articleId
                        console.log(" _id:", article._id);      //  _id
                        return String(fav.articleId._id) === String(article._id);
                    });

                    // 
                    console.log("", article.isFavorite, article._id);
                });


            } catch (error) {
                console.error(':', error)
            }
        },
    },

    created() {
        this.getuser()
        this.loadFavorites()
        this.categoriesfetchArticles()
    },
}
</script>

