<template>
    <div class="login-container" @mousedown="handleMouseDown" @mousemove="handleMouseMove" @mouseup="handleMouseUp">
        <!-- <transition name="fade">
            <Loader v-if="isLoading" :visible="isLoading" />
        </transition> -->
        <div v-if="!isLoading" class="carousel-container">
            <Carousel :carousel-items="carouselItems" />
        </div>
        <div class="card-container">
            <transition name="fade">
                <div v-if="isLoginForm" class="card">
                    <div class="card-inner">
                        <div class="card-front">
                            <LoginForm :form="form" :toggleForm="toggleForm" />
                        </div>
                    </div>
                </div>
            </transition>
            <transition name="fade">
                <div v-if="!isLoginForm" class="card">
                    <div class="card-inner">
                        <div class="card-back">
                            <RegisterForm :form="form" :toggleForm="toggleForm" />
                        </div>
                    </div>
                </div>
            </transition>
        </div>
    </div>
</template>

<script>
import Carousel from '../components/Carousel.vue';
import LoginForm from '../components/LoginForm.vue';
import RegisterForm from '../components/RegisterForm.vue';
import { Notification } from 'element-ui';
// import Loader from "@/components/Loader.vue";
export default {
    components: {
        Carousel,
        LoginForm,
        RegisterForm,
        // Loader
    },
    data() {
        return {
            form: {
                country: '',
                phone: '',
                email: '',
                password: '',
            },
            carouselItems: [
                {
                    id: 1, src: 'LunBoTuImg1.jpg', quote: '.', author: '(Where you invest, results will show.)'
                },
                { id: 2, src: 'LunBoTuImg2.jpg', quote: '.', author: '(Do what you love and do it well)' },
                { id: 3, src: 'LunBoTuImg3.jpg', quote: '.', author: "(If you're passionate about something, don't worry about failure. Keep at it and success will come naturally)" },
                { id: 4, src: 'LunBoTuImg4.jpg', quote: '.', author: "(Dreams don't run away, only you run away)" },
                { id: 5, src: 'LunBoTuImg5.jpg', quote: '. ', author: "(You can't change the past, but you can change the future)" }
            ],
            isLoginForm: true,
            mouseDownX: 0,
            mouseUpX: 0,
            isMouseDown: false,
            // isLoading: true, // 
        };
    },
    methods: {
        toggleForm() {
            this.isLoginForm = !this.isLoginForm;
        },
        handleMouseDown(event) {
            this.isMouseDown = true;
            this.mouseDownX = event.screenX;
        },
        handleMouseMove(event) {
            if (this.isMouseDown) {
                this.mouseUpX = event.screenX;
            }
        },
        handleMouseUp() {
            this.isMouseDown = false;
            if (this.mouseUpX - this.mouseDownX > 500) {
                this.goBack();
            }
        },
        goBack() {
            window.history.back();
        },
        showWelcomeNotification() {

            this.$nextTick(() => {
                setTimeout(() => {
                    Notification({
                        title: 'Welcome!',
                        message: 'Welcome .',
                        type: 'info',
                        duration: 4000,
                        offset: 20,
                    });
                }, 2000);
            });
        },
        handleTouchStart() { /* Implement touch start logic */ },
        handleTouchMove() { /* Implement touch move logic */ },
        handleTouchEnd() { /* Implement touch end logic */ }
    },
    mounted() {
        // this.isLoading = false;// 
        this.showWelcomeNotification();
        window.addEventListener('touchstart', this.handleTouchStart);
        window.addEventListener('touchmove', this.handleTouchMove);
        window.addEventListener('touchend', this.handleTouchEnd);
    },
    beforeDestroy() {
        window.removeEventListener('touchstart', this.handleTouchStart);
        window.removeEventListener('touchmove', this.handleTouchMove);
        window.removeEventListener('touchend', this.handleTouchEnd);
    },
};
</script>

