<template>
    <div class="form-container">
        <h2>Sign in</h2>
        <el-form @submit.prevent="handleSubmit" class="ElFrom">
            <el-form-item>
                <el-input class="ElInp" v-model="form.email" placeholder="Email"></el-input>
            </el-form-item>
            <el-form-item>
                <el-input class="ElInp" v-model="form.password" type="password" placeholder="Password"></el-input>
            </el-form-item>
            <el-button type="primary" class="BtnAft" @click="handleSubmit" block></el-button>
        </el-form>
        <div class="divider">
            <span class="line"></span>
            <span class="text">or</span>
            <span class="line"></span>
        </div>
        <div class="signin-link">
            <small>
                ? <a href="#" @click.prevent="toggleForm"></a>
            </small>
        </div>
        <!-- <div class="social-buttons">
            <el-button class="FengXiangBtn" type="default" block>Continue with Google</el-button>
            <el-button class="FengXiangBtn" type="default" block>Continue with Apple</el-button>
            <el-button class="FengXiangBtn" type="default" block>Continue with Twitter</el-button>
        </div> -->
    </div>
</template>

<script>
import axios from 'axios'; //  axios
import { Notification } from 'element-ui';
import { mapActions } from 'vuex'; //  map Vuex actions
export default {
    props: {
        toggleForm: {
            type: Function,
            required: true
        }
    },
    data() {
        return {
            form: {
                email: '',
                password: ''
            }
        };
    },

    methods: {
        ...mapActions(['updateUser']), //  Vuex actions
        async handleSubmit() {
            try {
                // 
                const requestData = {
                    email: this.form.email,
                    password: this.form.password
                };
                // console.log(requestData);
                // 
                const response = await axios.post('/api/auth/login', requestData, {
                    headers: {
                        'Content-Type': 'application/json'
                    }
                });

                // 
                Notification.success({
                    title: '',
                    message: '',
                    duration: 3000
                });

                //  token 
                const token = response.data.token;
                console.log('', response.data);
                const userDetails = response.data.user; // 

                localStorage.setItem('token', token);
                console.log("", userDetails);
                
                //  Vuex 
                this.$store.dispatch('updateUser', { token, details: userDetails });

                // 
                axios.get('/api/user', {
                    headers: { Authorization: `Bearer ${token}` }
                }).then(userResponse => {
                    // 
                    console.log("", userResponse);
                    const userDetails = userResponse.data;


                    //  Vuex 
                    this.$store.dispatch('updateUser', { token, details: userDetails });
                }).catch(userError => {
                    console.error('Get user details error:', userError);
                });

                // 
                this.$router.push('/Index'); // /Index 
            } catch (error) {
                // 
                console.log(error); // 
                if (error.response && error.response.data) {
                    // 
                    const errorMsg = error.response.data.msg || '';
                    Notification.error({
                        title: '',
                        message: errorMsg,
                        duration: 3000
                    });
                } else {
                    // 
                    console.log(error); // 
                    Notification.error({
                        title: '',
                        message: '',
                        duration: 3000
                    });
                }
            }
        }
    }
};
</script>

